@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #6228b1 ;
    font-family: "Lato",cursive;
}

.heading {
    text-transform: uppercase;
    font-size: 40px;
    color: white;
    margin: 30px 0;
    z-index: 1;
    text-align: center;
}

@media (max-width: 800px){
    .heading{
        margin: 15px 0;
        font-size: 35px;
    }
}

.input {
    display: flex;
    width: 95%;
    position: relative;
    align-items: center;
}

.inputBox {
    width: 100%;
    border-radius: 15px;
    padding: 20px 30px;
    font-size: 25px;
    border: none;
    transition: 0.2s;
    box-shadow: inset 0 0 5px black;
}

.inputSubmit {
    position: absolute;
    width: 150px;
    height: 50px;
    margin: 12px;
    border-radius: 15px;
    right: 0px;
    border: none;
    font-size: 15px;
    background-color: #7f52c2;
    color: white;
    transition: 0.2s all;
    box-shadow: 0 0 10px black;
}
.inputSubmit:hover{
    background-color: #9963ee;
}

.inputSubmit:active{
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    box-shadow: 0 0 5px black;
}

.toDos{
    display: flex;
    width: 30%;
    flex-direction: column;
    padding: 25px;
    border-radius: 5px;
    background-color: #cfb4ff;
}

.icon{
    margin-left: 10px;
    font-size: 25px;
    cursor: pointer;
}

.toDoSingle{
    display: flex;
    border-radius: 5px;
    padding: 20px;
    margin-top: 15px;
    background: rgba( 255, 255, 255, 0.25 );
    backdrop-filter: blur( 4px );
    -webkit-backdrop-filter: blur( 4px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    transition: 0.3s;
}

.toDoSingle:hover{
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
}

.todoSingleText{
    flex: 1 1;
    padding: 5px;
    border: none;
    font-size: 20px;
}


.container{
    display: flex;
    width: 95%;
    margin-top: 10px;
    justify-content: space-between;
    align-items: flex-start;
}

.toDosHeading{
    font-size: 20px;
    color: white;
}

.doing{
    background-color: orange;
}

.done{
    background-color: greenyellow;
}


@media(max-width: 700px)  {
    .input{
        width: 95%;
    }

    .toDos{
        width: 95%;
        margin-bottom: 10px;
    }

    .container{
        width: 95%;
        flex-direction: column;
    }

}

